.seccion_contacto{
    h3{
        text-align: center;
        font-size: 3em;
        margin: 5rem;
    }
    input{
        width: 25rem;
        margin-left: 10%;
        @include telefono{
            width: 80%;
            margin: 1rem auto;
        }
    }
}
.canales_body{
    .canales_texto{
        margin-top: 5rem;
        h2{
            font-size: 4em;
        }
        p{
            font-size: 3em;
        }
    }
}
.canales{
    display: flex;
    align-items: center;
    justify-content: space-around;
    gap: 2rem;
    @include tablet{
        flex-direction: column-reverse;
    }
    .img_canales{
        width: 50%;
        @include tablet{
            margin-top: 2rem;
            width: 100%;
        }
        img{
            width: 100%;
        }
    }
}
.canales_box{
    width: 50%;
    @include tablet{
        width: 100%;
    }
    .servicio{
        display: flex;
        justify-content: center;
        gap: 2rem;
        margin: 4rem 0;
        @include telefono{
            flex-direction: column;
        }
        .cn1{
            width: 100%;
            p{
                font-size: 2em;
                margin-bottom:2rem;
            }
            a{
                font-size: 2em;
                
            }
        }
    }
    
}
.formulario{
    display: flex;
    flex-direction: column;
    fieldset{
        border: 1px solid black;
    }
    legend{
        font-size: 2em;
        padding-bottom: 1.5rem;
    }
    label{
        font-size: 1.5em;
    }
    input{
        width  : 100%;
        height : 3rem;
        margin: 1rem 0;
        padding: 10px;
        font-size: 1.2em;
    }
    input:focus{
        cursor: auto;
    }
    textarea{
        width: 100%;
        margin-bottom: 1rem;
    }
    select{
        width: 20%;
        height: 2rem;
        font-size: 1.5em;
        @include tablet{
            width: 25%;
        }
        @include telefono{
            width: 100%;
        }
    }
    p{
       padding: 1.5rem;
       font-size: 1.8em; 
    }
}
.forma_contacto{
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 2rem; 
    input{
        width: 5rem;
        height: 2rem;
        @include telefono{
            width: 3rem;
    }
    }    
}
.btn-enviar{
    @include boton(flex, center, 20% ,1rem);
    margin-top: 2rem;
    color: white;
    @include telefono{
        width: 100%;
    }
}