html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-size: 100%;
  line-height: 1.3;
  font-family: "Merriweather", serif;
}

.contenedor {
  width: 90%;
  margin: 0 auto;
}

h1, h2, h3, h4, p {
  margin: 0;
}

h1 {
  font-size: 4em;
}

h2 {
  font-size: 3em;
}

h3 {
  font-size: 2.5em;
}

h4, p {
  font-size: 2em;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

.darkmode {
  background-color: #2c2c2c;
}
.darkmode .punto {
  background-color: #ffffff;
}
.darkmode .activo {
  background-color: #805e45;
}
.darkmode .sector, .darkmode .propiedades, .darkmode .beneficios {
  color: #b4b4b4;
}
.darkmode .seccion_social {
  color: #b4b4b4;
}
.darkmode .seccion_social a, .darkmode .seccion_social h4, .darkmode .seccion_social p {
  color: #b4b4b4;
}
.darkmode .testimonios h3 {
  color: #b4b4b4;
}
.darkmode .testimonios p {
  color: #000000;
}
.darkmode .testimonios blockquote::before {
  filter: invert(100%);
}
.darkmode .anuncio_propiedad {
  color: #b4b4b4;
}
.darkmode .anuncio_propiedad .lista_detalles img {
  filter: invert(100%);
}
.darkmode .seccion_contacto {
  color: #b4b4b4;
}
.darkmode .seccion_contacto a {
  color: #ffffff;
}
.darkmode .seccion_blog a, .darkmode .seccion_blog h3, .darkmode .seccion_blog h4, .darkmode .seccion_blog p {
  color: #b4b4b4;
}
.darkmode .blog h1, .darkmode .blog p {
  color: #b4b4b4;
}

.centrar_width {
  max-width: 80%;
  margin: 0 auto;
}

.mostrar_blanco {
  color: #ffffff;
}

.header {
  background-color: #805e45;
  width: 100%;
  box-shadow: 0px 12px 21px 0px rgba(0, 0, 0, 0.75);
}

/*contenedor menu*/
.header_box {
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding-top: 5rem;
}
@media (max-width: 480px) {
  .header_box {
    justify-content: space-between;
    flex-wrap: wrap;
  }
}
@media (max-width: 768px) {
  .header_box {
    padding: 4rem;
  }
}

/*logo*/
.logo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: #ffffff;
}
.logo h2 {
  text-transform: uppercase;
  font-size: 3.5em;
}
@media (max-width: 768px) {
  .logo h2 {
    font-size: 2.5em;
  }
}
@media (max-width: 480px) {
  .logo h2 {
    font-size: 2em;
  }
}
.logo p {
  margin: 0 auto;
  font-size: 2.5em;
}
@media (max-width: 480px) {
  .logo p {
    font-size: 2em;
  }
}

/*navegacion*/
.nav_principal {
  display: flex;
  align-items: center;
  transition: visibility 0.3s ease-in-out, opacity 0s ease-in-out, height 0.3s ease-in-out;
}
@media (max-width: 480px) {
  .nav_principal {
    display: none;
  }
}
.nav_principal .nav_box {
  display: flex;
  gap: 2rem;
  padding-right: 1rem;
  padding-right: 2rem;
}
@media (max-width: 768px) {
  .nav_principal .nav_box {
    gap: 1rem;
  }
}
@media (max-width: 480px) {
  .nav_principal .nav_box {
    padding-top: 2rem;
    align-items: center;
    transition: all 0.5s ease-in;
  }
}
.nav_principal .nav_box a {
  font-size: 2.5em;
  color: #ffffff;
}
@media (max-width: 768px) {
  .nav_principal .nav_box a {
    font-size: 1.8em;
  }
}
@media (max-width: 480px) {
  .nav_principal .nav_box a {
    font-size: 1.2em;
  }
}

/* menu mobile*/
.barra {
  display: none;
  width: 3rem;
  height: 3rem;
  margin: 2rem 0;
}
@media (max-width: 480px) {
  .barra {
    display: flex;
  }
}
.barra img {
  width: 100%;
}

@media (max-width: 480px) {
  .activar {
    display: flex;
  }
}

.btn-color {
  display: flex;
  align-items: center;
}
.btn-color .img-light-btn, .btn-color .img-dark-btn {
  display: block;
  width: 3rem;
  height: 3rem;
  filter: invert(100%);
}
.btn-color .img-light-btn:hover, .btn-color .img-dark-btn:hover {
  cursor: pointer;
}

.img-dark-btn {
  visibility: visible;
}

.img-light-btn {
  visibility: hidden;
}

.destacados {
  position: relative;
  height: 85%;
  width: 100%;
  overflow: hidden;
}
@media (max-width: 768px) {
  .destacados {
    height: 55%;
  }
}
@media (max-width: 480px) {
  .destacados {
    height: 60%;
  }
}

.galeria {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-flow: row nowrap;
  width: 300%;
  transition: all 0.5s ease;
  transform: translateX(0%);
}
@media (max-width: 768px) {
  .galeria {
    height: 50vh;
  }
}
@media (max-width: 480px) {
  .galeria {
    height: 55vh;
  }
}
.galeria img {
  width: 33.3333333333%;
  height: 90%;
}

.texto-destacados {
  position: absolute;
  bottom: 30%;
  left: 8.3333333333%;
  color: #ffffff;
  text-transform: uppercase;
}
@media (max-width: 768px) {
  .texto-destacados h1 {
    font-size: 2.5em;
  }
}
@media (max-width: 480px) {
  .texto-destacados h1 {
    font-size: 2em;
  }
}

.puntos {
  display: flex;
  justify-content: center;
  gap: 2rem;
  padding: 1rem;
  left: calc(16.6666666667% - 2rem);
}
.puntos .punto {
  border: 1px solid black;
  padding: 0.5rem;
  border-radius: 50%;
}

.activo {
  background-color: #e08f25;
}

.propiedades {
  padding-bottom: 2rem;
}
.propiedades h2 {
  font-size: 4em;
  padding: 5rem;
  text-align: center;
  text-transform: uppercase;
}
@media (max-width: 768px) {
  .propiedades h2 {
    font-size: 3em;
    padding: 3rem;
  }
}

.body_propiedades {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 3rem;
}
@media (max-width: 768px) {
  .body_propiedades {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
  }
}
@media (max-width: 480px) {
  .body_propiedades {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 3rem;
  }
}

.card_propiedades {
  position: relative;
  height: 100%;
  border: 1px solid black;
}
.card_propiedades img {
  width: 100%;
  height: 50%;
}

.texto_card {
  padding-left: 2rem;
  height: 30%;
}
.texto_card h3 {
  font-size: 3.5em;
  margin: 2rem 0;
}
@media (max-width: 768px) {
  .texto_card h3 {
    font-size: 3em;
    margin: 1rem;
  }
}
@media (max-width: 480px) {
  .texto_card h3 {
    font-size: 2.5em;
  }
}
.texto_card p {
  font-size: 2em;
  padding-bottom: 3rem;
}
@media (max-width: 768px) {
  .texto_card p {
    padding-bottom: 1rem;
  }
}
@media (max-width: 480px) {
  .texto_card p {
    font-size: 1.8em;
    padding-bottom: 1rem;
  }
}

.precio {
  font-size: 2.5em;
}

.detalles {
  border: 1px solid black;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  transition: all 0.7s ease-in;
  opacity: 0;
}

.detalles:hover {
  opacity: 1;
  background-color: #c7a68b;
}

.detalles_img {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  padding: 2rem;
  height: 100%;
}
.detalles_img li {
  display: flex;
  justify-content: center;
  flex-direction: column;
  height: 10vh;
}
.detalles_img li img {
  width: 9rem;
  height: 50%;
}
.detalles_img li p {
  text-align: center;
  font-size: 2.5em;
  padding-top: 2rem;
}

.btn-ver {
  background-color: #e08f25;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 80%;
  font-size: 2em;
  padding: 2rem;
  box-shadow: -31px 35px 17px -19px rgba(0, 0, 0, 0.75);
  padding-bottom: 1rem;
  margin: 1rem auto;
  transition: all 0.5s ease-in;
}
@media (max-width: 768px) {
  .btn-ver {
    margin: 2rem auto;
  }
}
.btn-ver a {
  color: white;
}

.btn-ver:hover {
  cursor: pointer;
  background-color: #e08f25;
  box-shadow: -31px 35px 17px -19px rgba(0, 0, 0, 0.75);
}

.mostrar {
  display: flex;
  justify-content: flex-end;
  position: relative;
}
.mostrar a {
  background-color: #e08f25;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20%;
  font-size: 2em;
  padding: 2rem;
  box-shadow: -31px 35px 17px -19px rgba(0, 0, 0, 0.75);
  margin: 2rem 0;
  color: #ffffff;
  box-shadow: 0px -6px 28px 0px rgba(0, 0, 0, 0.75);
  transition: all 0.5s ease;
  transition-property: background-color;
}
@media (max-width: 768px) {
  .mostrar a {
    width: 40%;
  }
}
@media (max-width: 480px) {
  .mostrar a {
    width: 100%;
    margin: 2rem auto;
  }
}
.mostrar a:hover {
  cursor: pointer;
  background-color: #e08f25;
}

.beneficios {
  text-align: center;
  margin: 2rem auto;
}
.beneficios h3 {
  font-size: 4em;
  text-transform: uppercase;
  margin: 3rem;
}
@media (max-width: 768px) {
  .beneficios h3 {
    font-size: 3em;
    margin: 2rem;
  }
}
@media (max-width: 480px) {
  .beneficios h3 {
    font-size: 2.5em;
  }
}

.beneficios-body {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
}
@media (max-width: 768px) {
  .beneficios-body {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 2rem;
  }
}

.card-beneficio {
  display: flex;
  flex-direction: column;
  justify-content: center;
  transition: all 0.5s ease-in-out;
}
.card-beneficio img {
  margin: 0 auto;
  width: 20rem;
  height: 20rem;
}
@media (max-width: 768px) {
  .card-beneficio img {
    width: 15rem;
    height: 15rem;
  }
}
@media (max-width: 480px) {
  .card-beneficio img {
    width: 10rem;
    height: 8rem;
  }
}
.card-beneficio h4 {
  text-align: center;
  font-size: 3.5em;
  margin: 2rem 0;
}
@media (max-width: 768px) {
  .card-beneficio h4 {
    font-size: 2.5em;
    margin: 1rem;
  }
}
@media (max-width: 480px) {
  .card-beneficio h4 {
    font-size: 2em;
    margin: 0;
  }
}
.card-beneficio p {
  font-size: 2.5em;
  padding: 2rem;
}
@media (max-width: 768px) {
  .card-beneficio p {
    font-size: 2em;
  }
}

.card-beneficio:hover {
  transform: scale(1.1);
}

.sector {
  text-align: center;
  height: 50%;
}
.sector h2 {
  font-size: 4em;
  padding: 2rem;
}
@media (max-width: 480px) {
  .sector h2 {
    font-size: 3em;
  }
}
@media (max-width: 768px) {
  .sector {
    height: 35%;
  }
}
@media (max-width: 480px) {
  .sector {
    height: 90%;
  }
}

.box-sector {
  height: 50%;
}
@media (max-width: 480px) {
  .box-sector {
    height: 80%;
  }
}

.areas {
  padding: 0;
  height: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
}
@media (max-width: 480px) {
  .areas {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 2rem;
    padding: 2rem 0;
  }
}

.zona {
  background-color: red;
  border: 2px solid black;
  position: relative;
  transition: all 0.5s ease;
  width: 90%;
  margin: 0 auto;
}
.zona img {
  width: 100%;
  height: 100%;
}
.zona .enlace {
  position: absolute;
  top: 45%;
  right: 10%;
  z-index: 10000;
}
@media (max-width: 768px) {
  .zona .enlace {
    left: 12.5%;
  }
}
@media (max-width: 480px) {
  .zona .enlace {
    top: 40%;
    left: calc(40% - 2rem);
  }
}
.zona .enlace a {
  color: white;
  font-size: 4em;
}
@media (max-width: 768px) {
  .zona .enlace a {
    font-size: 2.5em;
  }
}

.zona:hover {
  transform: scale(1.1);
  cursor: pointer;
}

.contacto {
  text-align: center;
  width: 100%;
  color: #ffffff;
  position: relative;
}
.contacto h2 {
  padding: 2rem;
  text-transform: uppercase;
  font-size: 4em;
}
@media (max-width: 768px) {
  .contacto h2 {
    font-size: 3.5em;
  }
}
@media (max-width: 480px) {
  .contacto h2 {
    font-size: 2.5em;
  }
}
.contacto img {
  width: 100%;
  height: 70%;
  background-size: cover;
}

.contacto_body {
  position: absolute;
  top: 50%;
  left: 15%;
  padding: 0 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media (max-width: 768px) {
  .contacto_body {
    height: 30rem;
    top: 45%;
    left: 0;
  }
}
.contacto_body h3 {
  font-size: 3.5em;
}
@media (max-width: 768px) {
  .contacto_body h3 {
    font-size: 2.5em;
  }
}
@media (max-width: 480px) {
  .contacto_body h3 {
    font-size: 2.5em;
  }
}
.contacto_body p {
  font-size: 2.5em;
}
@media (max-width: 480px) {
  .contacto_body p {
    font-size: 1.8em;
  }
}

.btn-contacto {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 10rem;
  padding: 2rem;
}
.btn-contacto a {
  background-color: #e08f25;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  font-size: 2em;
  padding: 2rem;
  box-shadow: -31px 35px 17px -19px rgba(0, 0, 0, 0.75);
  color: #ffffff;
  background-color: #805e45;
  font-size: 2.5em;
}
@media (max-width: 768px) {
  .btn-contacto a {
    font-size: 2.5em;
  }
}
@media (max-width: 480px) {
  .btn-contacto a {
    font-size: 2em;
  }
}

.seccion_social {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
