.footer{
    width: 100%;
    margin-top:3rem;
    background-color:$cafe;
    p{
        text-align: center;
        color: $blanco;
        font-size: 2.5em;
        @include telefono{
            font-size: 1.8em;
        }
    }
}
.footer_box{
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 90%;
    @include telefono{
        flex-direction: column;
    }
}
.redes_sociales{
    display: flex;
    justify-content: center;
    gap: 2rem;
    img{
        width: 4rem;
        height: 3rem;
        @include telefono{
            width: 3rem;
            margin: 1rem;
        }
    }
}
.nav_secundaria{
    .nav_box{
        display: flex;
        align-items: center;
        gap: 2rem;
        font-size: 2em;
        padding: 0;
        a{
            color: $blanco;
        }
        @include telefono{
            flex-direction: column;
        }
    }
}