.sector{
    text-align: center;
    height: 50%;
    h2{
        font-size: 4em;
        padding: 2rem;
        @include telefono{
            font-size: 3em;
        }
    }
    @include tablet{
        height: 35%;
        
    }
    @include telefono{
        height: 90%;
    }
    
}
.box-sector{
    height: 50%;
    @include telefono{
        height: 80%;
    }
}
.areas{
    padding:0;
    height:100%;
    @include grid(3, 2rem);
        @include telefono{
            @include grid(1, 2rem);
            padding: 2rem 0;
        }   
}
.zona{
    background-color: red;
    border: 2px solid black;
    position: relative;
    transition: all .5s ease;
    width: 90%;
    margin: 0 auto;
    img{
        width: 100%;
        height: 100%;
    }
    .enlace{
        position: absolute;
        top:calc(90% / 2);
        right:calc(20% / 2);
        z-index: 10000;
        @include tablet{
            left:calc(25% / 2);
        }
        @include telefono{
            top:calc(80% / 2);
            left:calc(80% / 2 - 2rem);
        }
        a{
            color: white;
            font-size: 4em;
            @include tablet{
                font-size: 2.5em;
            }
        }
    }
}
.zona:hover{
    transform: scale(1.1);
    cursor: pointer;
}