.anuncio_propiedad{
    width: 100%;
    h1{
        text-align: center;
        text-transform: uppercase;
        font-size: 4.5em;
        padding: 5rem;
    }
}
.anuncio{
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 2rem;
    height: 70%;
    position: relative;
    
    .img_anuncio{
        border: 2px solid $negro;
        width: 60%;
        
        img{
            width: 100%;
        }
    }
    .texto_anuncio{
        position: absolute;
        bottom: -80px;
        right: -50px;
        width: 50%;
        height: 35rem;
        background-color:$cafesuave;
        color: white;
        box-shadow: -31px 35px 17px -19px rgba(0,0,0,0.75);
        p{
            font-size: 2.2em;
            line-height: 1.5;
            padding: 2rem;

        }
    }
}
.barra_separadora{
    background-color: $crema;
    height: 10rem;
}
.detalles_casa{
    h2{
        text-align: center;
        font-size: 3.5em;
        text-transform: uppercase;
        margin: 5rem;
    }
}
.detalles_box{
    display: flex;
    justify-content: space-around;
    padding-bottom: 5rem;
    border-bottom: 2px solid $cafe;
    .lista_detalles{
        display: flex;
        justify-content: space-around;
        align-items: center;
        gap: 3rem;
        img{
            width: 6rem;
            height: 7rem;
            display: block;
            margin: 0 auto;
        }
        p{
            padding: 2rem;
            text-align: center;
            font-size: 2.5em;
        }
    }
}
.planos{
    img{
        width: 100%;
        height: 45rem;
    }
    
}
.ubicacion_propiedad{
    h3{
        text-align: center;
        margin:5rem;
        font-size: 3em;
        text-transform: uppercase;
    }
}
.ubicacion_box{
    display: flex;
    justify-content: space-around;
    align-items: center;
    gap: 2rem;
}
.detalles_ubicacion{
    position: relative;
    .sitio_interes{
        width: 100%;

        li{
            padding-bottom: 3rem;
            p{
                font-size: 2.5em;
            }
        }
    }
    
}
.sitio_interes::after{
    content: " ";
    display: flex;
    justify-content: center;
    align-items: center;    
    border-bottom: 1px solid $negro;
    border-left: 1px solid $negro;
    height: 35rem;
    width: 50rem;
    position: absolute;
    top: 0;
    left: 0;
    box-shadow: -31px 35px 17px -19px rgba(0,0,0,0.75);
}