html{
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after{
    box-sizing: inherit;
}
body{
    font-size: 100%;
    line-height: 1.3;
    font-family: $font-principal;
}
.contenedor{
    width: 90%;
    margin: 0 auto;
}
h1,h2,h3,h4,p{
    margin: 0;
}
h1{
    font-size: 4em;
}
h2{
    font-size: 3em;
}
h3{
    font-size: 2.5em;
}
h4,p{
    font-size: 2em;
}
li{
    list-style: none;
}
a{
    text-decoration: none;
}