.header{
    background-color: $cafe;
    width: 100%;
    box-shadow: 0px 12px 21px 0px rgba(0,0,0,0.75);
}
/*contenedor menu*/
.header_box{
    display: flex;
    align-items: center;
    justify-content: space-around;
    padding-top: 5rem;
    @include telefono{
        justify-content: space-between;
        flex-wrap: wrap;
    }
    @include tablet{
        padding: 4rem;
    }
}
/*logo*/
.logo{
    display: flex;
    flex-direction: column;
    justify-content: center;
     color: $blanco;
    h2{
        text-transform: uppercase;
        font-size: 3.5em;
       
        @include tablet{
            font-size: 2.5em;
        }
        @include telefono{
            font-size: 2em;
        }
    }
    p{
        margin: 0 auto;
        font-size: 2.5em;
        @include telefono{
            font-size: 2em;
        }
    }
}
/*navegacion*/
.nav_principal{
    display: flex;
    align-items: center;
    transition: visibility .3s ease-in-out, opacity .0s ease-in-out, height .3s ease-in-out;
    @include telefono{
        display: none;
    }
    .nav_box{
        display: flex;
        gap: 2rem;
        padding-right: 1rem;
            padding-right: 2rem;
        
        @include tablet{
            gap: 1rem;
        }
        @include telefono{
            padding-top: 2rem;

            align-items: center;
            transition: all .5s ease-in;
        }
        a{
            font-size: 2.5em;
            color: $blanco;
            
            @include tablet{
                font-size: 1.8em;
            }
            @include telefono{
                font-size: 1.2em;
            }
            
            
        }
    }
}
/* menu mobile*/
.barra{
    display: none;
    width: 3rem;
    height: 3rem;
    margin: 2rem 0;
    @include telefono{
        display: flex;
    }
    img{
        width: 100%;
    }
}
.activar{
    @include telefono{
        display: flex;
    }
    
}
.btn-color{
    display: flex;
    align-items: center;
    .img-light-btn, .img-dark-btn{
        display: block;
        width: 3rem;
        height: 3rem;
        filter: invert(100%);
        &:hover{
            cursor: pointer;
        }
    }
}
.img-dark-btn{
    visibility: visible;
}
.img-light-btn{
    visibility: hidden;
}
