.seccion_social{
    @include grid(2, 2rem);
    margin-top: 5rem;
    @include tablet{
        @include grid(1, 2rem);
        margin-top: 2rem;
    }
}
.blog{
    width: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2rem;
    margin: 0 auto;
    h1{
        font-size: 4.5em;
        padding: 2rem 0;
        @include telefono{
            font-size: 3em;
        }
    } 
    h3{
        font-size: 4em;
        padding: 3rem;
        text-align: center;
        @include tablet{
            font-size: 2.5em;
        }
    }
}
.entrada_blog{
    display: flex;
    gap: 2rem;
    align-items: center;
    @include tablet{
     flex-direction: column;   
    }
}
.img_blog{
    img{
        width: 100%;
        height: 40%;
        display: block;
    }
}
.texto_blog{
    h4{
        font-size: 3em;
        border-bottom: 2px solid $crema;
        color: $negro;
        @include tablet{
            font-size: 2.5em;
        }
    }
    .informacion_dato{
        padding: 2.5rem 0;
        span{
            color: red;
        }
    }
    p{
        font-size: 2.2em;
        color: $negro;
        @include tablet{
            font-size: 2em;
        }
    }
}
.entrada_p{
    line-height: 1.5;
    font-size: 2.5em;
    padding: 2rem 0;
    @include telefono{
        font-size: 2em;
        line-height: 1.2;
    }
}
