//mediaqueries
@mixin telefono{
    @media (max-width:$telefono){
        @content;        
    }
}
@mixin tablet{
    @media (max-width:$tablet){
        @content;        
    }
}
@mixin desktop{
    @media (max-width:$desktop){
        @content;        
    }
}
@mixin desktopHD{
    @media (max-width:$desktopHD){
        @content;        
    }
}
// grid
@mixin grid($columnas, $espacio){
    display: grid;
    grid-template-columns:repeat($columnas, 1fr);
    gap: $espacio;
}
//botones
@mixin boton($display,$posicion,$ancho, $espacio){
    background-color: $cafesuave;
    display: $display;
    justify-content: $posicion;
    align-items: center;
    width: $ancho;
    font-size: 2em;
    padding: $espacio;
    box-shadow: -31px 35px 17px -19px rgba(0,0,0,0.75);
}