.contacto{
    text-align: center;
    width: 100%;
    color: $blanco;
    position: relative;
    h2{
        padding: 2rem;
        text-transform: uppercase;
        font-size: 4em;
        @include tablet{
            font-size: 3.5em;
        }
        @include telefono{
            font-size: 2.5em;
        }   
    }
    img{
        width: 100%;
        height: 70%;
        background-size: cover;
    }
}
.contacto_body{
    position: absolute;
    top: calc(100% / 2);
    left: calc(30% / 2);
    padding: 0 2rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    @include tablet{
        height: 30rem;
        top: calc(90% / 2);
        left: 0;
    }
    h3{
       font-size: 3.5em;
       @include tablet{
          font-size: 2.5em;
       }
       @include telefono{
           font-size: 2.5em;
       }
    }
    p{
      font-size: 2.5em;
      @include telefono{
        font-size: 1.8em;
      }
    }
}
.btn-contacto{ 
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 10rem;
    padding: 2rem;
    a{
        @include boton(flex,center,100% ,2rem);
        color: $blanco;
        background-color: $cafe;
        font-size: 2.5em;
        @include tablet{
            
            font-size: 2.5em;
        }
        @include telefono{
            font-size: 2em;
        }
        
    }
    
}