.destacados{
    position: relative;
    height: 85%;
    width: 100%;
    @include tablet{
        height: 55%;
    }
    @include telefono{
        height: 60%;
    }
    overflow: hidden;   
}
.galeria{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-flow: row nowrap;
    width: 300%;
    transition: all .5s ease;
    transform: translateX(0%);
        @include tablet{
            height: 50vh;
        }
        @include telefono{
            height: 55vh;
        }
        img{
            width: calc( 100% / 3);
            height: 90%;
        }
}
.texto-destacados{
    position: absolute;
    bottom: calc(60% / 2);
    left: calc(25% / 3);
    color: $blanco;
    text-transform: uppercase;
    h1{
        @include tablet{
            font-size: 2.5em;
        }
        @include telefono{
            font-size: 2em;
        }
    } 
}
.puntos{
    display: flex;
    justify-content: center;
    gap: 2rem;
    padding: 1rem;
    left: calc(50% / 3 - 2rem);
    .punto{
        border: 1px solid black;
        padding: .5rem;
        border-radius: 50%;    
    }
}
.activo{
    background-color:$cafesuave;
}
