.testimoniales{
    h3{
        text-align: center;
        font-size: 4em;
        padding: 3rem;
        @include tablet{
            font-size: 2.5em;
        }
    }
}
.testimonios{
    background-color:$cafesuave;
    border-radius: 15px;
    margin: 5rem auto;
    position: relative;
    color: $blanco;
    box-shadow: -19px 10px 5px 0px rgba(0,0,0,0.75);
    @include tablet{
        width: 100%;
        margin: 2rem auto;
    }
    blockquote{
        padding: 5rem 0 0 3rem;
        width: 80%;
        margin: 0 auto;
        line-height: 1.8;
        @include tablet{
            padding-left:2rem;
            padding-top: 2rem;
        }
        @include telefono{
            line-height: 1.2;
        }
        p{
            padding: 4rem;
            font-size: 2.5em;
            @include tablet{
                padding: 2rem;
                font-size: 2em;
            }
            @include telefono{
                font-size: 1.8em;
            }

        }
    }
    p{
        text-align: end;
        padding: 4rem;
        font-size: 2.5rem;
        @include tablet{
            padding: 0 2rem 2rem 0;
            font-size: 2em;
        }
    }
}
.testimonios blockquote::before{
    content: "";
    background-image: url(/build/img/comilla.svg);
    background-repeat: no-repeat;
    width: 7rem;
    height: 8rem;
    display: block;
    position: absolute;
    left: 2rem;
    top: 5rem;
    @include telefono{
        width:6rem;
        height: 7rem;
        left: 1rem;
        top: 3rem;
    }
}