.beneficios{
    text-align: center;
    margin: 2rem auto;
    h3{
        font-size: 4em;
        text-transform: uppercase;
        margin: 3rem;
        @include tablet{
            font-size: 3em;
            margin: 2rem;
        }
        @include telefono{
            font-size: 2.5em;
            
        }
    }
}
.beneficios-body{
    
    @include grid(3, 2rem);
        @include tablet{
            @include grid(1 , 2rem);
            
        }
}
.card-beneficio{
    display: flex;
    flex-direction: column;
    justify-content: center;
    transition: all .5s ease-in-out;
    @include telefono{
        
    }
    img{
        margin: 0 auto;
        width: 20rem;
        height: 20rem;
        @include tablet{
            width: 15rem;
            height: 15rem;
        }
        @include telefono{
            width: 10rem;
            height: 8rem;
        }
    }
    h4{
        text-align: center;
        font-size: 3.5em;
        margin: 2rem 0;
        @include tablet{
            font-size: 2.5em;
            margin: 1rem;
        }
        @include telefono{
            font-size: 2em;
            margin: 0;
        }
    }
    p{
        font-size: 2.5em;
        padding: 2rem;
        @include tablet{
            font-size: 2em;
        }
    }
}
.card-beneficio:hover{  
    transform: scale(1.1);
}