.darkmode{
    background-color: $grisnoche;
    .punto{
        background-color: $blanco;
    }
    .activo{
        background-color:$cafe;
    }
    .sector, .propiedades , .beneficios{
        color: $gris;
    }
    .seccion_social{
        color: $gris;
        a ,h4,p{
            color: $gris;
        }
    }
    .testimonios{
        h3{
            color: $gris;
        }
        p{
            color: $negro;
        }
        blockquote::before{
           filter: invert(100%);
        }
        
    }
    .anuncio_propiedad{
        color: $gris;
        .lista_detalles{
            img{
                filter: invert(100%);
            }
            
        }
    }
    .seccion_contacto{
        color:$gris;
        a{
            color: $blanco;
        }
    }
    .seccion_blog{
        a,h3, h4, p{
            color: $gris;
        }
    }
    .blog{
        h1,p{
            color: $gris;
        }
    }
}
