
.propiedades{
    padding-bottom: 2rem;
     h2{
        font-size: 4em;
        padding: 5rem;
        text-align: center;
        text-transform: uppercase;
        @include tablet{
            font-size: 3em;
            padding: 3rem; 
        }
     }
}

.body_propiedades{
    @include grid( 3, 3rem); 
    @include tablet{
        @include grid(2, 3rem);
    }
    @include telefono{
        @include grid(1, 3rem);
    }
}

.card_propiedades{
    position: relative;
    height: 100%;
    border: 1px solid black;
    img{
        width: 100%;
        height: 50%;
    }
}
.texto_card{ 
    padding-left: 2rem;
    height: 30%; 
    h3{
        font-size: 3.5em;
        margin:2rem 0; 
        @include tablet{
            font-size: 3em;
            margin:1rem;
        }    
        @include telefono{
            font-size: 2.5em;
        }
    }
    p{
        font-size: 2em;
        padding-bottom: 3rem;
        @include tablet{
            padding-bottom: 1rem;
        }
        @include telefono{
            font-size: 1.8em;
            padding-bottom: 1rem;
        }
    }
}
.precio{
    font-size: 2.5em;
}
.detalles{
    border: 1px solid black;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    transition: all .7s ease-in;
    opacity: 0;
}
.detalles:hover{
    opacity: 1;
    background-color: $crema;
}
.detalles_img{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;   
    padding: 2rem;
    height: 100%;
    li{
        display: flex;
        justify-content: center;
        flex-direction: column;
        height:10vh;
        img{
            width: 9rem;
            height: 50%;
            
        }
        p{
            text-align: center;
            font-size: 2.5em;
            padding-top: 2rem
        }
    } 
}
.btn-ver{
    @include boton(flex,center,80%, 2rem);
    padding-bottom: 1rem;
    margin: 1rem auto;
    transition: all .5s ease-in;
    @include tablet{
       margin: 2rem auto; 
    }
    a{
        color: white;
        
    }
    
}
.btn-ver:hover{
    cursor: pointer;
    background-color: $cafesuave;
    box-shadow: -31px 35px 17px -19px rgba(0,0,0,0.75);
}
.mostrar{
    display: flex;
    justify-content: flex-end;
    position: relative;
    a{
        @include boton(flex,center,20% ,2rem);
        margin: 2rem 0;
        color: $blanco;
        box-shadow: 0px -6px 28px 0px rgba(0,0,0,0.75);
        transition: all .5s ease;
        transition-property:background-color;
         
        @include tablet{
            width: 40%;
        }
        @include telefono{
            width: 100%;
            margin: 2rem auto;
        } 
    }
    a:hover{
        cursor: pointer;
        background-color: $cafesuave;
    }
}
